@font-face {
  font-family: "M PLUS 1";
  font-weight: 100 900;
  font-display: swap;
  font-style: normal;
  font-named-instance: "Regular";
  src: url("Mplus1-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "M PLUS 1";
  font-weight: 600;
  font-display: swap;
  font-style: normal;
  font-named-instance: "Regular";
  src: url("Mplus1-SemiBold.woff2") format("woff2");
}
@font-face {
  font-family: "M PLUS 1 Code";
  font-weight: 100 900;
  font-display: swap;
  font-style: normal;
  font-named-instance: "Regular";
  src: url("Mplus1Code-Regular.woff2") format("woff2");
}
body {
  background-size: 2rem 2rem;
  background-image: linear-gradient(to right, rgba(160, 160, 160, 0.07) 1px, transparent 1px), linear-gradient(to bottom, rgba(160, 160, 160, 0.07) 1px, transparent 1px);
}
